<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="customerNo"
        label="客户号"
      >
      </el-table-column>
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="contractId"
        label="仓单编号（仓单总数）"
        :formatter="wwIdFormatter"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="currentPrice"
        label="当前价格"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="warningPrice"
        label="预警价格"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="pledgeRate"
        label="解除预警时间"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.updateTime)}}
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import * as ajaxUrl from "../ajax.config.js";

export default {
  data() {
    return {
      pageType: 2,
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam: {},
      programForm: {
        weight: "",
        ammount: ""
      },
      currentPage: 1,
      pageSize: 10,
      totalPage: 1,

      tableData: []
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText;
      this.axios.get(ajaxUrl.GETWARNINGINFOLIST, {
        params: {
          pageSize: this.pageSize,
          pageNum: this.currentPage,
          search: this.searchParam,
          status: 1,
          orderField: orderField,
          orderType: orderType
        }
      }).then(res => {
        this.tableData = res.data.resData;
        this.totalPage = res.data.page.total;
        this.pageSize = res.data.page.pageSize;
        this.currentPage = res.data.page.current;
        this.searchParam = {};
      }).catch(err => {
        console.log(err);
      });
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    openDialog(row, type) {
      
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    formatter(row, column) {
      return row.pledgeRate + "%";
    },
    //仓单显示格式转换
    wwIdFormatter(row, column) {
      if (row.warehouseWarrantList.length == 0) {
        return "-";
      } else if (row.warehouseWarrantList.length == 1) {
        return row.warehouseWarrantList[0].wwNo;
      } else if (row.warehouseWarrantList.length > 1) {
        return (
          row.warehouseWarrantList[0].wwNo +
          "~" +
          row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo +
          "(" +
          row.warehouseWarrantList.length +
          ")"
        );
      }
    },
    //时间格式转换
    dateFormat(data) {
      return this.common.fullTime(data);
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}

.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



